#menu-container {
  width: 100%;
  height: 100%;
  z-index: 6;
  background-color: var(--nav-background-clr-);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

#menu-container.show {
  display: flex;
}

.menu-top {
  width: 100%;
  justify-content: space-between;
  padding: 0 20px;
  font-size: 1.5rem;
  display: flex;
  position: absolute;
  top: 0;
}

.menu-top--items {
  height: 100%;
  padding: 16px 0 20px 11px;
}

.menu-items {
  width: 100%;
}

.menu-items ul li a {
  color: var(--menu-text-);
  text-decoration: none;
}

.menu-items ul li {
  justify-content: center;
  align-items: center;
  padding: 30px 0;
  display: flex;
}

.menu-items--social ul {
  justify-content: center;
  display: flex;
}

.menu-items--social ul li {
  padding: 30px 60px;
}

@media (min-width: 720px) {
  #menu-container {
    display: none;
  }
}

